import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const Panier = (props) => {
  const allArticlePanier = useSelector((state) => state.allArticlePanier);

  return (
    <div className="container-fluid">
      <div className="row p-2">
        {allArticlePanier.length === 0 ? (
          <div className="col-12 col-md-8 offset-md-2 text-left border-bottom pb-2 my-5">
            Votre panier est vide
          </div>
        ) : (
          <>
            <div className="col-12 col-md-8 offset-md-2 text-left px-3 mb-5">
              <Link to="/boutique">continuer les achats</Link>
            </div>
            <div className="col-12 col-md-8 offset-md-2 text-left px-md-0">
              <h4>Panier</h4>
            </div>
            <div className="col-12 col-md-6 offset-md-2 text-left px-0 page-panier">
              <ul className="w-100 d-md-flex px-0 ">
                <li className="small m-2">Produit</li>
                <li className="small m-2">Description</li>
                <li className="small m-2">Prix unit.</li>
                <li className="small m-2">Quantite</li>
                <li className="small m-2">Sous-total</li>
              </ul>
              {allArticlePanier.map((article) => (
                <ul className="w-100 d-md-flex flex-row px-0 ">
                  <li className="small m-2">
                    <img
                      src={process.env.PUBLIC_URL + article.image}
                      alt={article.titre}
                      className="photo-page-panier"
                    />
                  </li>
                  <li className="small m-2">{article.titre}</li>
                  <li className="small m-2">{article.prix}</li>
                  <li className="small m-2">{article.quantite}</li>
                  <li className="small m-2">
                    {article.prix * article.quantite}
                  </li>
                </ul>
              ))}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Panier;
